import { CircularProgress } from '@material-ui/core';
import auth from 'API-Service/auth';
import React, { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom';
import { useToasts } from 'react-toast-notifications';

import { magic } from 'utils/connectors';

function Callback() {
  const { addToast } = useToasts();
  const history = useHistory()
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const getRedirectResult = async () =>  {
      const result = await magic.oauth.getRedirectResult();
      console.log(result)
      const temp = JSON.parse(localStorage.getItem('temp'))
      auth
        .register(temp.email, 'any', true, result.magic.userMetadata.publicAddress.toLowerCase())
        .then(response => {
          if (response.status === 200) {
            addToast(`Successfully registered with your ${temp.provider} account`, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
            setTimeout(function(){history.push('/login');}, 1000);
          } else {
            addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
            history.push('/login')
          }
        }) 
      setLoading(false)
    }
    getRedirectResult()
  }, [])
  
  return (
    <>
      {loading && <CircularProgress /> }
    </>
  )
}

export default Callback