import { makeStyles, useTheme } from '@material-ui/styles';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  content: {
    backgroundColor: theme.palette.mainBackground,
    width: '576px',
    height: '100%',
    boxShadow: '0px 0px 20px grey',
  },
  public: {
    '& .ql-align-right': {
      textAlign: 'right'
    },
    '& .ql-align-left': {
      textAlign: 'left'
    },
    '& .ql-align-center': {
      textAlign: 'center'
    },
    color: theme.palette.gray,
    fontSize: '0.9375em',
 
    fontWeight: 400,
  },
  normalBlock: {
    padding: '30px 30px 30px 30px',
    // height: '100%',
    '& img': {
      width: '100%'
    },
    border: theme.palette.contrastColor
  },
  controlBlock: {
    padding: '30px 30px 70px 30px',
    height: '100%',
    textAlign: 'center',
    border: theme.palette.contrastColor
  },
  buttonBlock: {
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('ms')]: {
      display: 'flex',
      justifyContent: 'center',
    },
  },
  buttonText2: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "16px",
    lineHeight: "22px",
    display: "flex",
    alignItems: "center",
    color: "#FFFFFF",
    color: "#FFFFFF",
    padding: ".3rem .8rem ",
    
  },
  activeBtn: {
    background: "#6871EA",
    borderRadius: "9.91252px",
    '& span': {
      color: "#FFFFFF !important"
    },
    '&:hover': {
      background: '#6871EA',
    },
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  subHeader: {
    color: theme.palette.gray,
    fontSize: '0.9375em',
 
    fontWeight: 400,
    marginTop: '60px',
    marginBottom: '30px'
  },
  btnOpen: {
    '& .MuiButton-label': {
        textTransform: 'none',
        fontSize: '1.1em',
    },
    '& .MuiButton-containedSecondary:hover': {

    },
    '&:hover': {
        backgroundColor: theme.palette.pink
    },
    padding: '4px',
    border: 'none',
    fontWeight: '300',
    backgroundColor: theme.palette.pink,
    color: theme.palette.black_white,
    marginLeft: '20px',
    marginRight: '20px',
    marginBottom: '20px',
    paddingLeft: '40px',
    paddingRight: '40px',
    width: '400px',
    
  },
  progressContainer: {
    position: 'absolute',
    top: '50%',
    width: 'calc(100% - 50px)',
    display: 'flex',
    justifyContent: 'center',
  },
  progress: {
      color: theme.palette.pink
  },
  tooltip: {
    fontSize: '0.8em'
  },
  input: {
    position: 'relative',
    height: '40px',
    width: '300px',
    marginLeft: 'auto',
  },
  inputItem: {
    border: `1px solid ${theme.palette.search_bar_border}`,
    boxSizing: 'border-box',
    borderRadius: '8px',
    width: '300px',
    backgroundColor: 'transparent',
    paddingLeft: '42px',
    fontFamily: '"Lato", "sans-serif" ',
    outline: 'none',
    height: '40px',
    color: theme.palette.text_main,
    '&:focus': {
      border: `1px solid ${theme.palette.controller_color}`
    },
    '&::placeholder': {
      fontStyle: 'normal',
      fontWeight: '400',
      fontSize: '16px',
      display: 'flex',
      alignItems: 'center',
      color: theme.palette.text_sub,
    },
    [theme.breakpoints.down('xs')]: {
      width: '100% !important',
    }
  },
  nftHeader: {
    display: "flex",
    alignItems: 'center',
    justifyContent: 'space-between',
    '& button': {
      textTransform: "capitalize"
    },
    [theme.breakpoints.down('xs')]: {
      '& $lowestPrice': {
        width: 'min-content !important',
      }
    },

  },
  btnGrp: {
    textAlign: 'center',
    border: "1.23906px solid #ECECEE",
    borderRadius: "12.3906px",
    // '& .MuiButton-root:hover': {
    //   backgroundColor: "transparent",
    // }
  },
}));

export default useStyles;
