import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { ImageCard } from 'components';
import SvgSearch from "../../components/icons/Search";
import useclasses from './style';
import {
  Box,
  Grid,
  Button,
} from '@material-ui/core'

const Education = props => {
  const theme = useTheme();
  const classes = useclasses(theme);
  const [PageIndex, setPageIndex] = useState(0);
  const [searchTerm, setSearchTerm] = useState("");
  
  const modules = [
    {
      title: 'A Crypto Story',
      image: '/images/Topic-1-Image.jpg',
      description: 'Sometimes, the best way to understand a complex topic like',
      href: 'https://clutchwallet.xyz/a-crypto-story/',
      alt: 'A Crypto Story'
    },
    {
      title: 'The difference between tokens and coins',
      image: '/images/Topic-2-Image.png',
      description:
        'People often confuse the difference between tokens and coins',
      href:
        'https://clutchwallet.xyz/what-is-the-difference-between-tokens-and-coins/',
      alt: 'The difference between tokens and coins'
    },
    {
      title: 'The Internet and Blockchain',
      image: '/images/Topic-3-Image.png',
      description:
        'Inevitably, the internet was the inception of the technology revolution',
      href: 'https://clutchwallet.xyz/blockchain-and-the-internet/',
      alt: 'The Internet and Blockchain'
    },
    {
      title: 'What the heck is an NFT?',
      image: '/images/Topic-4-Image.png',
      description: 'If youve been paying attention to the crypto industry',
      href: 'https://clutchwallet.xyz/what-the-heck-nft/',
      alt: 'What the heck is an NFT?'
    },
  ];

  const guides = [
    {
      title: 'Topic 4: The Internet',
      image: 'https://clutchwallet.xyz/wp-content/uploads/2021/09/Sarah-v2.png',
      description:
        'Topic 4 The Internet Introduction Cryptocurrency is primarily exchanged through the internet, so to gain',
      href: 'https://clutchwallet.xyz/topic-1-a-crypto-story/',
      alt: 'The Internet and Blockchain'
    },
    {
      title: 'Crypto Industry  High Level',
      image: 'https://clutchwallet.xyz/wp-content/uploads/2021/12/Crypto-Guide-cover-image.png',
      description:
        'Bec provides a high level view of the various components that make up the crypto industry',
      href: 'https://clutchwallet.xyz/crypto-industry-high-level/',
      alt: 'The Internet and Blockchain'
    },
    {
      title: 'How to get started in crypto',
      image: 'https://clutchwallet.xyz/wp-content/uploads/2021/12/How-to-get-started-Cover-Image-2.jpg',
      description:
        'I get these questions a lot  how do I get started with crypto? Where do I go? What should I do?.',
      href: 'https://clutchwallet.xyz/how-to-get-started-in-crypto/',
      alt: 'The Internet and Blockchain'
    },
    {
      title: 'Make and List an NFT on Opensea.io',
      image: 'https://clutchwallet.xyz/wp-content/uploads/2022/03/Clutch-Mobile.jpg',
      description:
        'This is for all my creatives, artists, hustlers and people creating value in the world.',
      href: 'https://clutchwallet.xyz/make-and-list-nft-on-opensea/',
      alt: 'The Internet and Blockchain'
    },
    {
      title: 'How to keep your crypto secure',
      image: 'https://clutchwallet.xyz/wp-content/uploads/2022/01/Hybrid-Blockchain-1.png',
      description:
        'Crypto security is a hot topic as scammers around the world took home a record $14 billion in cryptocurrency in 2021,',
      href: 'https://clutchwallet.xyz/how-to-keep-your-crypto-secure/',
      alt: 'The Internet and Blockchain'
    },
  ];

  const [articles , setArticles] = useState(guides)

  const [isGuides, setIsGuides] = useState(true)
  const [isModules, setIsModules] = useState(false)
  const handleToggle = (type) => {
    setIsGuides(!isGuides);
    setIsModules(!isModules);
    
    if(type === 'guides') {
      setArticles(guides);
    } else if(type === 'modules') {
      setArticles(modules);
    }
  }

  return (
    <Box>
      <Grid
        container spacing={1}
        style={{ marginTop: "1rem", marginBottom: "1.5rem" }}
        display="flex" 
        justifyContent="space-between" 
        alignItems="center"
        // className={classes.header}
      >
        <Grid item xs={12} sm={6} md={4} lg={4} xl={4} 
        >
          <Box className={classes.nftHeader}>
            <Box 
              style={{background: "#fff", padding: "5px"}} 
              className={classes.btnGrp}>
              <Button
                className={isGuides ? classes.activeBtn : ""}
                onClick={() => handleToggle('guides')}
              >
                <span 
                  className={classes.buttonText2}
                  style={{color: "#6D6F96"}}
                > Guides </span>
              </Button>
              <Button
                className={isModules ? classes.activeBtn : ""}
                onClick={() => handleToggle('modules')}
              >
                <span 
                  className={classes.buttonText2} 
                  style={{color: "#6D6F96"}}
                > Modules </span>
              </Button>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={12} sm={6} md={4} lg={4} xl={4}>
          <Box className={classes.input}>
            <Box>
              <SvgSearch
                style={{
                  position: "absolute",
                  left: 12,
                  top: 14,
                  transform: "scale(0.70)",
                }}
              />
            </Box>
            <input
              onChange={(e) => {
                setPageIndex(0);
                setSearchTerm(e.target.value);
              }}
              placeholder="Search"
              className={classes.inputItem}
            />
          </Box>
          
        </Grid>
      </Grid>

      <Grid
        container spacing={1}
        style={{marginTop:'1rem'}}
      > 
        {
          articles.filter((article) =>
            article["title"]
              .toLowerCase()
              .includes(searchTerm.toLowerCase())
            ).map((item, index) => {
            return (
              <Grid 
                key={index} 
                item 
                xs={12} sm={6} md={4} lg={3} xl={3}
                style={{padding: ".5rem"}}
              >
                <ImageCard
                  title={item.title}
                  image={item.image}
                  text={item.description}
                  href={item.href}
                />
              </Grid>
            );
          })
        }
      </Grid>
    </Box>
  );
};

export default Education;
