import { Box, Button, CircularProgress, Dialog, Grid, Modal } from '@material-ui/core';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { setExpertMode } from 'store/actions/App';
import Typography from 'theme/typography';
import { FirstIcon, SecondIcon } from './icons/icon';
import useStyles from './style';

const ExpertModal = props => {
  const { open, handleClose } = props;
  const classes = useStyles();
  const [step, setStep] = useState(0);
  const [isExpert, setIsExpert] = useState(false);
  const dispatch = useDispatch();
  
  return (
    console.log(open),
    <Grid container spacing={1}> 
        <Grid item xs={12} sm={12} md={12}> 
          <Dialog onClose={handleClose} open={open} className={classes.dialog} disableBackdropClick>
            {
              step == 0 ? 
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Welcome to Clutch Wallet
                </Box>
                <FirstIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Connect to an existing wallet, create your own Clutch Wallet, or do both! Continue Reading to learn more.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(1)}>
                    Next
                  </Button>
                </Box>
              </>
              :
              step == 1 ?
              <>
                <Box className={classes.header} style={{marginTop: '33px'}}>
                  Before you start
                </Box>
                <Box className={classes.subHeader}>
                  Are you:
                </Box>
                <Box style={{display: 'flex', justifyContent: 'center', marginBottom: 52}}>
                  <Box style={{marginRight: 35}} onClick={() => setIsExpert(false)}>
                    <Box className={!isExpert ? classes.selectedImageBox : classes.imageBox}/>
                    <Box className={!isExpert ? classes.selectedTextBox : classes.textBox}> First Timer</Box>
                  </Box>
                  <Box onClick={() => setIsExpert(true)}>
                    <Box className={!isExpert ? classes.imageBox : classes.selectedImageBox} />
                    <Box className={!isExpert ? classes.textBox : classes.selectedTextBox}> Expert </Box>
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => setStep(isExpert ? 2 : 3)}>
                    Let's Start!
                  </Button>
                </Box>
              </>
              :
              step == 2 ?
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Enjoy!
                </Box>
                <SecondIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    Connect to an existing wallet, create your own Clutch Wallet, or do both! Continue Reading to learn more.
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => {
                    dispatch(setExpertMode(true)); localStorage.setItem('expert', 'true');
                  }}>
                    Finish
                  </Button>
                </Box>
              </>
              :
              <>
                <Box className={classes.header} style={{margin: '33px'}}>
                  Congratulations!
                </Box>
                <SecondIcon style={{margin: 'auto'}}/>
                <Box className={classes.content_wrapper}>
                  <Box className={classes.content}>
                    <Box>
                      Clutch is your HQ that allows you to manage everything in one place - NFTs, crypto, and more. 
                    </Box>
                    <Box style={{marginTop: 30}}>
                      If you need additional help, please see our Guides.
                    </Box>
                  </Box>
                </Box>
                <Box className={classes.controller}>
                  <Button variant="contained" className={classes.connect_button} onClick={() => {
                    dispatch(setExpertMode(true)); localStorage.setItem('expert', 'true');
                  }}>
                    Finish
                  </Button>
                </Box>
              </>
            }
          </Dialog>
        </Grid>
    </Grid>
    
  );
};

export default ExpertModal;
