import React, { useEffect, useRef, useState } from 'react';
import { SiteInfoContextConsumer } from 'App';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { AppBar, Box, Button, Card, Fade, Paper, Popover, useMediaQuery } from '@material-ui/core';
import useStyles from './style';
import { useHistory } from 'react-router-dom';
import { DarkIcon, EthLogoIcon, HamburgerIcon, LightIcon } from './svgs/icons';
import SearchInput from 'components/SearchInput';
import SearchImg from 'components/SearchImg';
import { setCurrentWallet, setTrendingData, setWalletList, setAddressList, setExpertMode } from 'store/actions/App';
import { useDispatch, useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';
import { KeyboardArrowDown, KeyboardArrowUp } from '@material-ui/icons';
import WalletCard from '../WalletCard';
import { ConnectWalletModal } from 'components';
import { ETH_ADDRESS, MAIN_NETWORK_ID, WALLETS, WETH_ADDRESS } from 'utils/constants';
import { switchNetwork } from 'web3/network';
import useActiveWeb3React from "hooks/web3-react"
import util from 'API-Service/util';
import { getBalanceWithMultiCall, getBlockTimestamp, getCorrectDecValue } from 'web3/contracts';

const Topbar = props => {
  const {
    className,
    onSidebarOpen,
    onSidebarClose,
    openSidebar,
    ...rest
  } = props;
  const { activate } = useActiveWeb3React();

  const dispatch = useDispatch();
  const walletList = useSelector(state => state.app.walletList);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const [walletModalOpen, setWalletModalOpen] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const classes = useStyles();
  let history = useHistory();

  const max400 = useMediaQuery('(max-width:400px)');

  const onMaxTopbar = () => {
    if (openSidebar === false) onSidebarOpen();
    else onSidebarClose();
  };

  useEffect(() => {
    let _walletList = JSON.parse(localStorage.getItem('wallets') || '[]');
    let _currentWallet = localStorage.getItem('currentWallet');
    let _trendingData = JSON.parse(localStorage.getItem("trendingData") || '[]');
    let _addressList = JSON.parse(localStorage.getItem("addressList") || '[]');
    let _expert = localStorage.getItem("expert");
    dispatch(setExpertMode(_expert == 'true'));
    setWallets(_walletList)
    dispatch(setTrendingData(_trendingData));
    dispatch(setAddressList(_addressList));
    if (_walletList.length != 0) {
      dispatch(setCurrentWallet(_currentWallet));
    }
  }, []);

  const setWallets = async (_walletList) => {
    for (let i = 0; i< _walletList.length; i ++) {
      let balance = await getTokenList(_walletList[i].address);
      _walletList[i].balance = balance;
    }
    dispatch(setWalletList(_walletList));
    localStorage.setItem('wallets', JSON.stringify(_walletList))
  }

  const getTokenList = async (address) => {
    const res = await util.getWalletTokenData(address)
    console.log(res)
    let tokenInfoList = res.data.walletTokenData.map((item, index) => { return {address: item.tokenAddress, name: item.name, symbol: item.symbol, decimals: item.decimals}});
    tokenInfoList = [{address: ETH_ADDRESS, name: 'Ethereum', symbol: 'ETH', decimals: 18}, ...tokenInfoList];
    const balanceList = await getBalanceWithMultiCall(tokenInfoList, address);
    const tokenListInfo = tokenInfoList.map((item, index) => {
      return {
        ...item,
        balance: balanceList.data[index]
      }
    })

    const tokens = tokenListInfo.map((item, index) => {return item.address === ETH_ADDRESS ? WETH_ADDRESS : item.address});
    let _tokenList = JSON.stringify(tokens);
    _tokenList = _tokenList.replace("\'", "\"");
    
    const _res = await util.getTokenListInfo(_tokenList);
    const tokenPriceInfo = _res.data.latestTokenData;
    const finalTokenInfoList = tokenPriceInfo.map((item, index) => {
      return {
        ...item,
        ...tokenListInfo[index],
      }
    });
    let sum = 0;
    for (let i = 0;i < finalTokenInfoList.length; i ++) {
      let a = finalTokenInfoList[i];
      sum += Number(getCorrectDecValue(a.latestPrice * a.balance, a.decimals, 4, false))
    }

    return sum.toFixed(0)
  }

  useEffect(() => {
    const item = walletList.find(({address}) => address === currentWallet);
    if (item) {
      const wallet = WALLETS.find(({title}) => title === item.type)
      if (wallet) {
        handleConnectWallet(wallet, item)
      }
    }
  }, [currentWallet, walletList.length])

  const handleConnectWallet = (walletInfo, currentWalletInfo) => {
    const { connector, type } = walletInfo;
  
    if (connector) {
      if (type != 'magic')
        activate(connector, undefined, true)
          .then(async res => {
            await switchNetwork(MAIN_NETWORK_ID)
          })
          .catch(error => {
            if (error) {
              activate(connector);
            } else {
              if (error.code == 4001) {
              } else if (type === 'metamask') {
              }
              console.info('Connection Error - ', error);
            }
          })
          .finally(() => {});
        else {
          console.log(currentWalletInfo)
          activate(currentWalletInfo.provider, currentWalletInfo.email)
        }
    }
  };

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <SiteInfoContextConsumer>
      {props => (
        <AppBar
          {...rest}
          className={clsx(classes.root, className)}>
          <div className={classes.toolbar}>
            <div className={classes.leftContainer}>
              <div className={classes.titlebar}>
                <div
                  className={classes.close_drawer_icon}
                  onClick={onMaxTopbar}>
                  <HamburgerIcon />
                </div>
              </div>
              <div className={classes.searchbar}>
                <SearchInput placeholder="Search" />
              </div>
            </div>
            <div className={classes.rightControllerArea}>
              <div className={classes.searchIcon}>
                <SearchImg />
              </div>
              <div
                className={classes.helper}
                onClick={e => {
                  e.preventDefault();
                  props.toggleContrast();
                }}>
                {props.is_contrast ? <DarkIcon /> : <LightIcon />}
              </div>
              <div className={classes.avatar}>
                {walletList.length == 0 ? (
                  <Button
                    variant="contained"
                    className={classes.connect_button}
                    onClick={() => {
                      setWalletModalOpen(true);
                    }}>
                    Connect Wallet
                  </Button>
                ) : (
                  <Button
                    variant="contained"
                    className={classes.connect_button}
                    onClick={handleClick}>
                    <EthLogoIcon className={classes.eth_icon} />
                    <Box>{getReducedWalletAddress(currentWallet)}</Box>
                    {!open ? <KeyboardArrowDown /> : <KeyboardArrowUp />}
                  </Button>
                )}
              </div>
            </div>
          </div>
          <Popover
            className={classes.popover}
            open={open}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            style={{width: max400? "100%" : "auto"}}
            >
              <Card className={classes.card}>
                <WalletCard handleClose={handleClose} setWalletModalOpen={setWalletModalOpen} walletModalOpen={walletModalOpen}/>
              </Card>
          </Popover>
          <ConnectWalletModal 
            open={walletModalOpen}
            handleClose={() => setWalletModalOpen(false)}
          />
        </AppBar>
      )}
    </SiteInfoContextConsumer>
  );
};

Topbar.propTypes = {
  className: PropTypes.string,
  onSidebarOpen: PropTypes.func
};

export default Topbar;
