import axios from 'axios';
import { BIT_CONFIG, USDT_ADDRESS } from 'utils/constants';

class Util {
    getSwapChart = (fromAddress, toAddress, fromDate) => {
        return axios
            .post(process.env.REACT_APP_QUERY_API, {
                query:
                    `{
                        tokenPricesData(
                        from: ${fromDate},
                        baseCurrencies: ["${fromAddress}"],
                        quoteCurrency: "${toAddress}"
                        chainId: 1,
                        ) {
                            baseCurrency,
                            quoteCurrency,
                            prices {
                            quotePrice,
                            transactionTime,
                        },
                        }
                }`}
            )
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    getWalletTokenData = (walletAddress) =>{
        return axios
            .post(process.env.REACT_APP_QUERY_API, {
                query: 
                    `{
                        walletTokenData(
                          chainId: 1,
                          walletAddress: "${walletAddress}",
                        ) {
                          tokenAddress
                          name
                          symbol
                          decimals
                        }
                    }`
            }).then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }
    getTokenBalanceListFromWallet = (walletAddress, fromDate) => {
        return axios
            .post(process.env.REACT_APP_QUERY_API, {
                query:
                    `{
                        transferData(
                          from: ${fromDate},
                          chainId: 1,
                          walletAddress: "${walletAddress}",
                        ) {
                          walletAddress,
                              tokenAddress,
                              history{
                                  amount
                                  isSent
                                  transactionTime
                          },
                        }
                    }`}
            )
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    getTokenListInfo = (tokenList) => {
        return axios
            .post(process.env.REACT_APP_QUERY_API, {
                query:
                    `{
                        latestTokenData(
                          baseCurrencies: ${tokenList},
                          quoteCurrency: "${USDT_ADDRESS}",
                          chainId: 1,
                        ) {
                          baseCurrency,
                          quoteCurrency,
                          latestPrice,
                          change,
                        }
                }`}
            )
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    getTokenListPriceInfo = (tokenList, fromDate) => {
        return axios
            .post(process.env.REACT_APP_QUERY_API, {
                query:
                    `{
                        tokenPricesData(
                          from: ${fromDate},
                          baseCurrencies: ${tokenList},
                          quoteCurrency: "${USDT_ADDRESS}",
                          chainId: 1,
                        ) {
                              baseCurrency,
                              quoteCurrency,
                              prices {
                              quotePrice,
                            transactionTime,
                          },
                        }
                  }`}
            )
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    getTrendingCrypto = (network, address, quoter) => {
        return axios
            .post(process.env.REACT_APP_BITQUERY_API, {
                query: `
                {
                    ethereum(network: ${network}) {
                            dexTrades(
                              options: {desc: "timeInterval.minute", limit: 1}
                              protocol: {is: "Uniswap v2"}
                              baseCurrency: {is: "${address}"}
                              quoteCurrency: {is: "${quoter}"}
                            ) {
                              exchange {
                                name
                              }
                              timeInterval {
                                minute(count: 1)
                              }
                              baseCurrency {
                                symbol
                                address
                              }
                              baseAmount
                              quoteCurrency {
                                symbol
                                address
                              }
                              close_price: maximum(of: time, get: quote_price)
                            }
                          }
                }`
            }, BIT_CONFIG)
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }
}
export default new Util();