import { Box, Button, Dialog, IconButton, Grid } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import useStyles from './style';
import useActiveWeb3React from "hooks/web3-react"
import { ETH_ADDRESS, MAIN_NETWORK_ID, WALLETS, WETH_ADDRESS } from 'utils/constants';
import { Wallet, ConnectModal, ConnectMagicModal } from 'components';

import { CloseIcon } from './icons/icon';
import { useDispatch, useSelector } from 'react-redux';
import { setCurrentWallet, setWalletList, setCurrentWalletInfo } from '../../store/actions/App';
import { switchNetwork } from 'web3/network';
import { getBalanceWithMultiCall, getBlockTimestamp, getCorrectDecValue } from 'web3/contracts';
import util from 'API-Service/util';
import { useWeb3React } from '@web3-react/core';

const ConnectWalletModal = props => {
  const { open, handleClose } = props;
  const classes = useStyles();
  const { activate, deactivate, account, library, chainId } = useWeb3React();
  const currentWalletInfo = useSelector(state => state.app.currentWalletInfo);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const [modalOpen, setModalOpen] = useState(false);
  const [magicModalOpen, setMagicModalOpen] = useState(false);

  const dispatch = useDispatch();

  useEffect(() => {
    console.log(account)
    if (account && currentWalletInfo) {
      getTokenList(account)
      dispatch(setCurrentWallet(account.toLowerCase()));
      localStorage.setItem('currentWallet', account.toLowerCase())
      // if (currentWalletInfo.type !== 'fortmatic')
      //   dispatch(setCurrentWalletInfo(null));
    }
  }, [account, currentWalletInfo]);

  const getTokenList = async (address) => {
    const res = await util.getWalletTokenData(address)
    console.log(res)
    let tokenInfoList = res.data.walletTokenData.map((item, index) => { return {address: item.tokenAddress, name: item.name, symbol: item.symbol, decimals: item.decimals}});
    tokenInfoList = [{address: ETH_ADDRESS, name: 'Ethereum', symbol: 'ETH', decimals: 18}, ...tokenInfoList];
    const balanceList = await getBalanceWithMultiCall(tokenInfoList, address);
    const tokenListInfo = tokenInfoList.map((item, index) => {
      return {
        ...item,
        balance: balanceList.data[index]
      }
    })

    const tokens = tokenListInfo.map((item, index) => {return item.address === ETH_ADDRESS ? WETH_ADDRESS : item.address});
    let _tokenList = JSON.stringify(tokens);
    _tokenList = _tokenList.replace("\'", "\"");
    
    const _res = await util.getTokenListInfo(_tokenList);
    const tokenPriceInfo = _res.data.latestTokenData;
    const finalTokenInfoList = tokenPriceInfo.map((item, index) => {
      return {
        ...item,
        ...tokenListInfo[index],
      }
    });
    let sum = 0;
    for (let i = 0;i < finalTokenInfoList.length; i ++) {
      let a = finalTokenInfoList[i];
      sum += Number(getCorrectDecValue(a.latestPrice * a.balance, a.decimals, 4, false))
    }
    let walletList = JSON.parse(localStorage.getItem('wallets') || '[]');
    if (!walletList.find(checkWallet)) {
      walletList.push({ address: account.toLowerCase(), type: currentWalletInfo.title, balance: sum.toFixed(0) });
      localStorage.setItem('wallets', JSON.stringify(walletList));
    }
    dispatch(setWalletList(walletList));
  }

  const checkWallet = wallet => {
    return wallet.address === account.toLowerCase();
  };

  const handleModalClose = () => {
    setModalOpen(false);
  };

  const handleMagicModalClose = () => {
    setMagicModalOpen(false);
  };

  const handleConnectWallet = walletInfo => {
    const { connector, type } = walletInfo;
    console.log(walletInfo)
    dispatch(setCurrentWalletInfo(walletInfo));

    if (walletInfo.type === 'fortmatic') 
      handleClose(true);
    else
      setModalOpen(true)
    if (connector) {
      activate(connector, undefined, true)
        .then(async res => {
          await switchNetwork(MAIN_NETWORK_ID)
          setTimeout(() => {
            setModalOpen(false);
          }, 2000);
        })
        .catch(error => {
          if (error) {
            // activate(connector);
            setModalOpen(false);
          } else {
            setModalOpen(false);
            if (error.code == 4001) {
            } else if (type === 'metamask') {
            }
            console.info('Connection Error - ', error);
          }
        })
        .finally(() => {});
    }
  };

  return (
    <Grid container spacing={1}> 
      <Grid item xs={12} sm={12} md={12} className={'classes.paper'}>
        <Dialog onClose={handleClose} open={open} className={classes.dialog}>
          <Box className={classes.header}>
            <Box>Connect new wallet</Box>
            <Box className={classes.icon} onClick={handleClose}   
              style={{marginTop: ".4rem", marginRight: '5px'}}>
              <IconButton
                color="primary"
                aria-label="upload picture"
                component="span"
                className={classes.control_button}>
                <CloseIcon />
              </IconButton>
            </Box>
          </Box>
          <Box className={classes.divider} />
          <Grid container spacing={2}>
            {
              WALLETS.map((item, index) => (
                <Wallet
                  src={item.src}
                  title={item.title}
                  image_type={item.image_type}
                  onClick={() => index == 7 ? setMagicModalOpen(true) : handleConnectWallet(item)}
                  key={index}
                />        
              ))
            }
          </Grid>
          <ConnectModal
            open={modalOpen}
            handleClose={handleModalClose}
            account={account}
            walletInfo={currentWalletInfo}
          />
          <ConnectMagicModal
            open={magicModalOpen}
            handleClose={handleMagicModalClose}
          />
        </Dialog>
      </Grid>
    </Grid>
  );
};

export default ConnectWalletModal;
